.App{
  min-height: 100vh;
  background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}

.header{
  padding: 10px;
  margin-left: 10px;
  display: flex;
  font-family: 'Patrick Hand', cursive;
  
}

.foodongle{
  margin-right: 10px;
}
.foodiz{
  box-shadow: 0px 5px 20px rgb(71, 71,71);
  border-radius: 50px 50px 50px 50px;
}
  
  

input::placeholder {
  font-size: 2vmin;
}

/* .foodongle{
  
  border: 1px solid #ccc;
  float: left;
  width: 20%;
} */

.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  
  
}

.search-bar{
  width:50%;
  border: none;
  border-radius: 50px 0px 0px 50px;
  padding: 10px;
  box-shadow: 0px 5px 20px rgb(71, 71,71);
  

}  

.search-button{
  background: lightcoral;
  border: none;
  border-radius: 0px 50px 50px 0px;
  padding: 10px 20px;
  color: white;
  box-shadow: 0px 5px 20px rgb(71, 71,71);

}

.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}